import Engine from "../Engine";
import { DecoupledCommand } from "../interfaces/ICommand";
import { Datamap } from "../Datamap";
import { getRandomInt } from "../util";

export default class GameTime {

    constructor(public engine: Engine) {

    }
    
    get data () {
        return this.engine.datamap.gameTime;
    }

    reset = () => {
        this.engine.datamap.gameTime = GameTimeInit();
    }

    static get requiredMS () {
        return 24000;
    }
    static maxDays = 30;
    static maxMonths = 12;


    processDelta = (delta: number) => {
        this.data.currentRun.time += delta;
        

        if (this.engine.datamap.prophetData.devotionUpgrades.abc.a) {
            delta = delta * 2;    
        }
        if (this.engine.datamap.pantheon.time.b) {
            delta = delta * (1 + .1 * this.engine.datamap.pantheon.time.b);
        }
   

        let mscost = GameTime.requiredMS;
        this.data.msprogress += delta;
        if (this.data.msprogress >= mscost) {
            this.passDayAction();
        }
    }

    processCommands = (commands: string[]) => {
        switch (commands[1]) {
            case 'passday':
                this.passDay();
                break;

            case 'eatday':
                this.eatDay();
                break;
        
            default:
                console.log('error in gametime processCommands')
                break;
        }
    }

    passDayAction = () => {

        
        
        let daysForTriggers = Math.floor(this.data.msprogress / GameTime.requiredMS)
        
        //console.log(`skipping ${daysForTriggers} days worth of triggers because my code is garbage, sorry`);
        
        this.dayTriggers(daysForTriggers);
        
        this.multiday();
        /*
        this.data.day ++;
        if (this.data.day > GameTime.maxDays) {
            this.data.day -= GameTime.maxDays;
            this.data.month ++; 
            if (this.data.month > GameTime.maxMonths) {
                this.data.month -= GameTime.maxMonths;
                this.data.year ++;
            }
        }
        */


    }

    multiday = () => {
        let mscost = GameTime.requiredMS;
        let startms = this.data.msprogress;

        let elapsed_days = Math.floor(this.data.msprogress / mscost);
        this.data.msprogress = this.data.msprogress % GameTime.requiredMS;
        
        let totalDays = elapsed_days + this.data.day;
        this.data.day = ((totalDays - 1) % 30) + 1;
        
        let elapased_months = Math.floor((totalDays - 1) / 30);
        
        let totalMonths = this.data.month + elapased_months;
        this.data.month = ((totalMonths- 1)%12)+1;
        
        let gainedYears = Math.floor((totalMonths - 1) / 12)
        this.data.year +=  gainedYears;
        
        //console.log('multiday!', startms, elapsed_days, elapased_months, gainedYears);
        if (elapased_months > 0 && this.engine.datamap.offline.happening === false && this.engine.datamap.autosave === true) {
            this.engine.save(); 
        }

    }

    dayTriggers(days: number) {

        let baseBonus = Math.floor( this.engine.datamap.pantheon.time.c / 10 );
        let rngGoal = this.engine.datamap.pantheon.time.c % 10;

        //need to get rid of all of this//
        let i = 0
        for (i = 0; i < days; i++) {
            
            let rng = getRandomInt(1,10);
            let bonus = baseBonus;
            
            if (rng <= rngGoal) {
                bonus ++;
            }
        
            this.engine.datamap.pantheon.time.bonusDays += bonus;
            this.engine.prophet.dayTrigger(1 + bonus);
            this.engine.datamap.rocketData.joules ++;
            this.engine.adornments.dayTrigger();
        
            this.engine.answers.automateDays();
            //this.engine.notify();
            //console.log('day',i);
            
        }
        //console.log('days done',i);


    }

    passDay = () => {
        this.passDayAction()
    }

    eatDay = () => {
        if (this.data.day < 2) return;
        this.data.day --;
        this.data.eatenDays ++;
    }
    
}

export interface GameTimeData {
    msprogress: number;
    day: number;
    month: number;
    year: number;
    eatenDays: number;
    currentRun: {
        time: number;
    }
}

export function GameTimeInit (): GameTimeData {
    return {
        day: 1,
        month: 1,
        year: 0,
        msprogress: 0,
        eatenDays: 0,
        currentRun: {
            time: 0
        }
    }
}

export const COMMAND_EAT_DAY: DecoupledCommand = {
    name: 'Eat Day',
    command: 'gametime eatday',
    able: (datamap:Datamap) => {
        return datamap.gameTime.day > 1;
    }
}

export const COMMAND_pass_day: DecoupledCommand = {
    name: "Pass Day",
    command: 'gametime passday',
}
