import AbstractModule from "./AbstractModule";
import ExpoCurve, {  } from "../interfaces/ExpoCurve";
import { Datamap } from "../Datamap";
import { getRandomInt } from "../util";
import { USD_UpgradeTithe, USD_Upgrade_Pamphlets } from "./USDPurchases";
import { expoCommand_buy } from "../interfaces/ExpoCommand";

export default class Answers extends AbstractModule {

    get data () {
        return this.engine.datamap.answers
    }
 
    handle_answer_command = (cmdArray: string[]) => {
        switch (cmdArray[1]) {
            case 'pray':
                this.answer_pray(cmdArray[2]==='up')
                break;

            case 'make':
                this.answer_make(cmdArray[2]==='up')
                break;
            
            case 'polish':
            case 'invest':
            case 'holypower':
            case 'recruit':
            case 'superAnswerWorkers':
                this.modify_key(cmdArray[1],cmdArray[2]==='up')
                break;
            case 'upgrade':
                this.modify_upgrade(cmdArray[2]);
                break;



            default:
                console.log('error in answers.ts', cmdArray);
                    
                break;
        }
    }

    modify_key = (key: string, up:boolean) => {
        console.log(key, up);
        
        if (up) {
            if (availaleQuestion(this.engine.datamap) > 0) this.data[key]++;
        } else {
            this.data[key] = Math.max(0, this.data[key] - 1)       
        }
    }

    modify_upgrade = (key:string) => {
        //get cost
        let cost = gemUpgradeForAnswerCost(key)
        //return if broke
        if (cost > this.engine.datamap.adornments.gems) return;
        //check for existence of the key in the upgrade data
        if (Object.keys(this.data.gemUpgrades).includes(key)) {
            //sanity return for already bought - stops double buys
            if (this.data.gemUpgrades[key]) return

            //remove gems -- buy
            this.engine.datamap.adornments.gems -= cost;
            //set data -- buy
            this.data.gemUpgrades[key] = true;
        }
    }

    answer_pray (up:boolean) {
        if (up) {
            if (availaleQuestion(this.engine.datamap) > 0) this.data.pray = this.data.pray + 1
        } else {
            this.data.pray = Math.max(0, this.data.pray - 1)
        }
    }

    answer_make (up:boolean) {
        if (up) {
            if (availaleQuestion(this.engine.datamap) > 0) this.data.make = this.data.make + 1
        } else {
            this.data.make = Math.max(0, this.data.make - 1)
        }
    }

    automateDays = () => {

        this.automateMake();

        if (this.data.invest > 0) {
            for (let index = 0; index < this.data.invest; index++) {
                let rng = getRandomInt(0,1)
                let upgrades = [USD_UpgradeTithe, USD_Upgrade_Pamphlets]
                expoCommand_buy(this.engine.datamap, upgrades[rng])                
            }
        }

        if (this.data.polish > 0) {
            for (let index = 0; index < this.data.polish; index++) {
                let rng = getRandomInt(0,9);
                this.engine.prophet.upgradeRelic(rng.toString())
            }
        }

        this.automatePray();

        if (this.data.holypower > 0) {
            this.engine.datamap.prophetData.holyPower = Math.floor(Math.pow(.9999,this.data.holypower)*this.engine.datamap.prophetData.holyPower)
        }

        if (this.data.recruit > 0) {
            for (let index = 0; index < this.data.recruit; index++) {
                let rng = getRandomInt(0,2);
                this.engine.followers.recruitParitioner(rng.toString())
            }
        }

        this.superAnswerDay();

    }

    superAnswerDay = () => {
        if (this.data.superAnswerWorkers < 1) return;

        this.data.superAnswerProgress += this.data.superAnswerWorkers;
        
        this.checkSuperAnswerTick();
    }

    checkSuperAnswerTick = () => {
        let goal = superAnswerGoal(this.data.superAnswerAnswers);
        if (this.data.superAnswerProgress >= goal) {
                this.data.superAnswerProgress -= goal;
                this.data.superAnswerAnswers ++;
        }
    }

    automatePray = () => {
        if (this.data.pray) this.engine.prophet.prayCount(this.data.pray);
    }

    automateMake () {
        for (let index = 0; index < this.data.make; index++) {
            let rng = getRandomInt(0,9);
            this.engine.prophet.buyRelic(rng.toString())
        }
    }
    

    reset = () => {
        this.engine.datamap.answers = AnswerData_init();
    }

}

export function usedQuestions  (data: Datamap)  {
    return data.answers.pray + data.answers.make + data.answers.polish + data.answers.invest + data.answers.holypower + data.answers.superAnswerWorkers + data.answers.recruit
}

export function totalQuestions  (data:Datamap)  {
    return data.prophetData.congregation.paritioners[2]
}

export function availaleQuestion (data:Datamap) {
    return totalQuestions(data) - usedQuestions(data)
}

export function superAnswerGoal (superAnswerAnswers: number) {
    return 10 * superAnswerAnswers;
}

export function superAnswerBoxVisible (superAnswerProgress: number, superAnswerAnswers: number) {
    return superAnswerProgress > 0 || superAnswerAnswers > 0
}

export function gemUpgradeForAnswerCost(key: string) {
    if (key === ANSWER_DATA[0][0]) return 10;
    if (key === ANSWER_DATA[1][0]) return 100;
    if (key === ANSWER_DATA[2][0]) return 100;
    if (key === ANSWER_DATA[3][0]) return 100;
    if (key === ANSWER_DATA[4][0]) return 1000;
    if (key === ANSWER_DATA[5][0]) return 1000;
    if (key === ANSWER_DATA[6][0]) return 1;


    return 69;
}



export const ANSWER_DATA: [string, string, string][] = [
    ['pray','Pray for the Piety of the Church','Unlock upgrades'],
    ['make','Make Relics for the Church','Unlock selection'],
    ['invest', 'Invest in Congregation Upgrades','Unlock selection'],
    ['polish', "Upgrade Relics with the Church's USD",'Unlock selection'],
    ['holypower', 'Consume Holy Power to increase its effectiveness','Unlock discretion'],
    ['recruit', 'Recruit a random believer with the Church`s Devotion','Unlock discretion'],
    ['superAnswerWorkers', 'Recruit people to join again... in their next life.','Unlock information'],
]

export interface AnswerData {
    pray: number;
    make: number;
    invest: number;
    polish: number;
    holypower: number;
    recruit: number;
    superAnswerWorkers: number;
    superAnswerProgress: number;
    superAnswerAnswers: number;
    [key:string]: number | any;
    gemUpgrades: {
        pray: boolean;
        make: boolean;
         invest: boolean;
        polish: boolean;
        holypower: boolean;
        recruit: boolean;
        superAnswerWorkers: boolean;
        [key:string]: boolean;
    }
}

export function AnswerData_init(): AnswerData {
    return {
        pray: 0,
        invest: 0,
        make: 0,
        polish: 0,
        holypower: 0,
        superAnswerWorkers: 0,
        superAnswerProgress: 0,
        superAnswerAnswers: 0,
        recruit: 0,
        gemUpgrades: {
            holypower: false,
            invest: false,
            make: false,
            polish: false,
            pray: false,
            recruit: false,
            superAnswerWorkers: false
        }
    }
}

