import AppContext from "../context/context";
import React from "react";
import { formatNumber } from "../engine/util";
import { prophet_maxHolyPower, relic_getAllProductionPerecond, preachers_max_deovtion_gain, devotee_devotion_gain, prophet_prestige_gain, net_Piety_gain, prayer_Piety_seconds_gain, daily_Piety_prayer_gain } from "../engine/modules/Prophet";
import { WealthyBeliever_USD_gain, net_USD_gain, net_Devotion_gain } from "../engine/modules/Followers";
import { currentSalary } from "../engine/modules/Exploring";
import TipFC from "./comps/TipFC";

const ProphetUI: React.FC<{}> = () => (
    <AppContext.Consumer>{({ data, submitCommand }) => (

        <div className='ProphetUI'>

            <PietyEstimate/>

            <div hidden={data.prophetData.congregation.USD === 0} className='USDDiv'>
            {`$${formatNumber(data.prophetData.congregation.USD)}`}<br/>
            {`+ ${formatNumber(net_USD_gain(data))}  per day`}
            </div>

            <div hidden={data.prophetData.congregation.unlocked === false} className='DevotionDiv'>
                {`Devotion: ${formatNumber(data.prophetData.devotion)} `}<br/>
                {`+ <= ${formatNumber(net_Devotion_gain(data))} per day`}
            </div>

            <div hidden={data.prophetData.holiness === 0} className='HolinessDiv'>
                    {`Holiness: ${formatNumber(data.prophetData.holiness)}`}<br/>
                    {`+ ${formatNumber(prophet_prestige_gain(data))} on Reincarnation`}
            </div>

        </div>

    )}</AppContext.Consumer>
)

export default ProphetUI;

const PietyEstimate: React.FC<{}> = () => (
    <AppContext.Consumer>{({ data, submitCommand }) => {
            let piety_ps = relic_getAllProductionPerecond(data);
            let piety_pp = piety_ps * prayer_Piety_seconds_gain(data);
            let piety_pd = piety_pp * daily_Piety_prayer_gain(data);
            let s1: [number, string] = [piety_ps, "per second"];
            let s2: [number, string] = [piety_pp, "per prayer"];
            let s3: [number, string] = [piety_pd, "per day"];
            let l = [s1, s2, s3].sort((a,b)=>b[0]-a[0]);
        
        return (


            <div className='PietyDiv'>

                    {`Piety: ${formatNumber(data.prophetData.piety)}`}<br/>
                    <span>
                    {`+ ${formatNumber(l[0][0])} ${l[0][1]} `}
                        <TipFC tip={<div>
                    {`+ ${formatNumber(l[1][0])} ${l[1][1]} `}<br/>
                    {`+ ${formatNumber(l[2][0])} ${l[2][1]} `}
                        </div>}/>

                    </span>
            </div>

    )}}</AppContext.Consumer>
)