import AppContext from "../../context/context";

import './PantheonUI.scss'

import React from "react";
import CommandButtonFC from "./CommandButtonFC";
import { BasePantheonUpgrade, pantheonPurchase_able, pantheonPurchaseCost, pantheonPurchase_maxCost, pantheonPurchase_maxAmmount } from "../../engine/modules/Pantheon/Types";
import CommandButton2 from "./CommandButton2";
import * as TIME from '../../engine/modules/Pantheon/Time';
import TipFC from "./TipFC";
import Pantheons from '../../engine/modules/Pantheon/Pantheons';
import { formatNumber } from "../../engine/util";

const PantheonUI: React.FC = () => (
    <AppContext.Consumer>
        {({data, submitCommand}) => (
            <div className='PantheonUI'>
                <span className='HeaderText'>
                    Pantheon
                </span>
                <div>
                    Upgrades, once bought, persist through normal reincarnations ... and work even before you add that God to your pantheon. Reincarnate to choose new gods for your Pantheon. 
                </div>
                <div>
                
                    <TipFC tip={`Not 100% usefull yet. Only the first tier of god (Proud) has additional upgrades. Feel free to suggest upgrades for the missing ones.`}/>

                </div>
                <div className='pantheoncolumn'>

                
                <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.abc.a}
                    dps={[Pantheons.TIME.Upgrade_A,Pantheons.TIME.Upgrade_B, Pantheons.TIME.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Time
                    </span>          
                </PantheonUpgradeBox_v2>


                    <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.abc.b}
                    dps={[Pantheons.PRODUCTION.Upgrade_A,Pantheons.PRODUCTION.Upgrade_B, Pantheons.PRODUCTION.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Production
                    </span>          
                </PantheonUpgradeBox_v2>

                

                    <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.abc.c}
                    dps={[Pantheons.WEALTH.Upgrade_A,Pantheons.WEALTH.Upgrade_B, Pantheons.WEALTH.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Wealth
                    </span>          
                </PantheonUpgradeBox_v2>


                <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.def.a}
                    dps={[Pantheons.PERSUASION.Upgrade_A,Pantheons.PERSUASION.Upgrade_B, Pantheons.PERSUASION.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Persuasion
                    </span>          
                </PantheonUpgradeBox_v2>

                <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.def.b}
                    dps={[Pantheons.MAINTAINANCE.Upgrade_A,Pantheons.MAINTAINANCE.Upgrade_B, Pantheons.MAINTAINANCE.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Maitainance
                    </span>          
                </PantheonUpgradeBox_v2>

                <PantheonUpgradeBox_v2 active={data.prophetData.devotionUpgrades.def.c}
                    dps={[Pantheons.GREED.Upgrade_A,Pantheons.GREED.Upgrade_B, Pantheons.GREED.Upgrade_C]}>
                    <span className='HeaderText'>
                    God of Greed
                    </span>          
                </PantheonUpgradeBox_v2>
                

                </div>
            </div>
        )}
    </AppContext.Consumer>
)

export default PantheonUI;


const PantheonUpgradeComp: React.FC<{ dp: BasePantheonUpgrade }> = (props) => (
    <AppContext.Consumer>
        {({ data }) => (
            <div className='PietyCommand flexcolumn pantheonupgrade'>
            <CommandButton2 {...props.dp.command}
                cost={(data)=>pantheonPurchaseCost(data,props.dp)}
                able={(data)=>pantheonPurchase_able(data, props.dp)}
                costName='Piety'
                tip={<div>
                    Cost: {formatNumber(pantheonPurchaseCost(data, props.dp))}<br />
                    {props.dp.description}<br />
                    Level : {props.dp.countFn(data)}
                </div>}
            />
            <CommandButton2 name={()=>`Level ${props.dp.countFn(data)} - Buy Max (${pantheonPurchase_maxAmmount(data,props.dp)})`} command={(data)=>props.dp.command.command(data)+'max'} 
                        able={(data)=>pantheonPurchase_able(data, props.dp)}
                        />
            </div>
        )}
    </AppContext.Consumer>
)

const PantheonUpgradeBox_v2: React.FC<{active: boolean, dps: BasePantheonUpgrade[]}> = (props) => (
    <AppContext.Consumer>
        {({data}) => (
            <div>
                {props.children}
                <div>
                {props.dps.map((dp, index)=>(<PantheonUpgradeComp_v2 dp={dp} active={props.active} key={'pantehon'+index}  />))}
                </div>
                
            </div>
        )}
    </AppContext.Consumer>
)

const PantheonUpgradeComp_v2: React.FC<{ dp: BasePantheonUpgrade, active:boolean }> = (props) => (
    <AppContext.Consumer>
        {({ data }) => (
            <div className={props.active?'PietyCommand flexcolumn pantheonupgrade':'flexcolumn pantheonupgrade'}>
            <CommandButton2 {...props.dp.command}
                cost={(data)=>pantheonPurchaseCost(data,props.dp)}
                able={(data)=>pantheonPurchase_able(data, props.dp) && props.active}
                costName={'Piety'}
                tip={<div>
                    Cost: {formatNumber(pantheonPurchaseCost(data, props.dp))}<br />
                    {props.dp.description}<br />
                    Level : {props.dp.countFn(data)}
                </div>}
            />
            {<CommandButton2 name={()=>`Level ${props.dp.countFn(data)} - Buy Max (${pantheonPurchase_maxAmmount(data,props.dp)})`} command={(data)=>props.dp.command.command(data)+'max'} 
                        able={(data)=>pantheonPurchase_able(data, props.dp) && props.active}
                        />}
            
            </div>
        )}
    </AppContext.Consumer>
)
